* {
  margin: 0px;
  padding: 0px; }

body {
  font-family: "calibri",sans-serif; }

#header {
  height: 50px;
  width: 100%;
  background: #dadada; }
  #header .innerheader {
    position: relative;
    width: 80%;
    height: 100%;
    margin: auto; }
    #header .innerheader ul {
      background: #dadada;
      list-style-type: none;
      position: relative; }
      #header .innerheader ul li {
        height: 100%;
        width: 150px;
        padding: 0px 10px;
        position: relative;
        float: left; }
        #header .innerheader ul li.phone {
          border: 0px solid white;
          border-left: 0px solid white; }
        #header .innerheader ul li.mail {
          border: 0px solid grey;
          border-left: 0px solid white;
          border-right: 0px solid white; }
        #header .innerheader ul li a {
          display: block;
          line-height: 50px;
          height: 100%;
          width: 100%;
          text-decoration: none;
          font-size: 16px;
          font-family: fantasy, sans-serif;
          text-align: center;
          color: #e23c52; }
        #header .innerheader ul li button {
          height: 40px;
          padding: 0 20px;
          border: 0px;
          border-radius: 3px; }
          #header .innerheader ul li button.Signin {
            background: #e23c52;
            color: white;
            margin-top: 5px;
            margin-bottom: 5px; }
          #header .innerheader ul li button.register {
            background: white;
            color: #e23c52;
            -webkit-box-shadow: 0px 0px 5px 0px #e23c52;
                    box-shadow: 0px 0px 5px 0px #e23c52;
            margin-top: 5px;
            margin-bottom: 5px; }
    #header .innerheader .floaterRight {
      float: right;
      list-style-type: none; }

